#login-form h2 {
  color:rgba(255,255,255,.8);
  margin-left:12px;
  margin-bottom: 0.83em;
}

body {
  background: #272125;
  font-family: 'Roboto Condensed', sans-serif;
  color: #F2F2F2;
}

#login-form form {
  position:relative;
  margin: 50px auto;
  /* width: 380px; */
  height: auto;
}

#login-form input {
  padding: 16px;
  border-radius:7px;
  border:0px;
  background: rgba(255,255,255,.2);
  display: block;
  margin: 15px;
  width: calc(90% - 35px);
  color:white;
  font-size:18px;
  height: 54px;
}

#login-form input:focus {
  outline-color: rgba(0,0,0,0);
  background: rgba(255,255,255,.95);
  color: #e74c3c;
}

#login-form button {
  float: right;
  height: 121px;
  width: calc(7% + 15px);
  border: 0px;
  background: #e74c3c;
  border-radius:7px;
  padding: 10px;
  color:white;
  font-size:22px;
}

#login-form .inputUserIcon {
  position:absolute;
  top: 71px;
  right: calc(10% + 35px);
  color:white;
}

#login-form .inputPassIcon {
  position:absolute;
  top: 142px;
  color:white;
  right: calc(10% + 35px);
}

#login-form input::-webkit-input-placeholder {
  color: white;
}

#login-form input:focus::-webkit-input-placeholder {
  color: #e74c3c;
}

body > .container{
  padding-top: 50px;
}

