@import url(http://weloveiconfonts.com/api/?family=entypo);
@import url(http://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Lato:400,300,700,900,100);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,300,700);
/* zocial */
[class*="entypo-"]:before {
  font-family: 'entypo', sans-serif; }

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

#login-form h2 {
  color: rgba(255, 255, 255, 0.8);
  margin-left: 12px;
  margin-bottom: 0.83em; }

body {
  background: #272125;
  font-family: 'Roboto Condensed', sans-serif;
  color: #F2F2F2; }

#login-form form {
  position: relative;
  margin: 50px auto;
  /* width: 380px; */
  height: auto; }

#login-form input {
  padding: 16px;
  border-radius: 7px;
  border: 0px;
  background: rgba(255, 255, 255, 0.2);
  display: block;
  margin: 15px;
  width: calc(90% - 35px);
  color: white;
  font-size: 18px;
  height: 54px; }

#login-form input:focus {
  outline-color: transparent;
  background: rgba(255, 255, 255, 0.95);
  color: #e74c3c; }

#login-form button {
  float: right;
  height: 121px;
  width: calc(7% + 15px);
  border: 0px;
  background: #e74c3c;
  border-radius: 7px;
  padding: 10px;
  color: white;
  font-size: 22px; }

#login-form .inputUserIcon {
  position: absolute;
  top: 71px;
  right: calc(10% + 35px);
  color: white; }

#login-form .inputPassIcon {
  position: absolute;
  top: 142px;
  color: white;
  right: calc(10% + 35px); }

#login-form input::-webkit-input-placeholder {
  color: white; }

#login-form input:focus::-webkit-input-placeholder {
  color: #e74c3c; }

body > .container {
  padding-top: 50px; }

button, .btn {
  border-radius: 0px;
  border-width: 2px;
  padding: 1px 8px;
  background: transparent;
  text-shadow: none;
  color: inherit;
  font-size: 17px;
  font-family: 'Roboto Condensed';
  letter-spacing: 2px; }

button a {
  color: inherit; }

button a:hover {
  text-decoration: none; }

button.btn-default:hover, .btn.btn-default:hover {
  background-color: #ccc;
  border-color: #ccc;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6); }

.form-control, form {
  height: 26px;
  border-radius: 0;
  background-color: transparent;
  color: inherit;
  border-color: #F2F2F2 !important; }

label, p, span {
  font-weight: 500;
  text-shadow: none;
  font-size: 19px; }

.form-group {
  margin-bottom: 7px; }

.page-header .glyphicon {
  font-size: 23px; }

.page-header-wrap .lead {
  text-transform: uppercase; }

.h1, h1 {
  font-size: 29px; }

nav {
  font-family: Lato; }

.page-header-wrap {
  font-family: Lato; }

.page-header {
  margin-bottom: 0px;
  font-size: 50px; }

/*-------------------------
    Simple reset
--------------------------*/
* {
  margin: 0;
  padding: 0; }

/*-------------------------
    General Styles
--------------------------*/
/*----------------------------
    The file upload form
-----------------------------*/
#upload {
  font-family: 'PT Sans Narrow', sans-serif;
  background-color: #373a3d;
  background-image: -webkit-linear-gradient(top, #373a3d, #313437);
  background-image: -moz-linear-gradient(top, #373a3d, #313437);
  background-image: linear-gradient(top, #373a3d, #313437);
  width: 250px;
  padding: 30px;
  border-radius: 3px;
  height: auto;
  margin: 200px auto 100px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); }

#drop {
  background-color: #2E3134;
  padding: 40px 50px;
  margin-bottom: 30px;
  border: 20px solid transparent;
  border-radius: 3px;
  border-image: url("../img/border-image.png") 25 repeat;
  text-align: center;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  color: #7f858a; }

#drop a {
  background-color: #007a96;
  padding: 12px 26px;
  color: #fff;
  font-size: 14px;
  border-radius: 2px;
  cursor: pointer;
  display: inline-block;
  margin-top: 12px;
  line-height: 1; }

#drop a:hover {
  background-color: #0986a3; }

#drop input {
  display: none; }

#upload ul {
  list-style: none;
  margin: 0 -30px;
  border-top: 1px solid #2b2e31;
  border-bottom: 1px solid #3d4043; }

#upload ul li {
  background-color: #333639;
  background-image: -webkit-linear-gradient(top, #333639, #303335);
  background-image: -moz-linear-gradient(top, #333639, #303335);
  background-image: linear-gradient(top, #333639, #303335);
  border-top: 1px solid #3d4043;
  border-bottom: 1px solid #2b2e31;
  padding: 15px;
  height: 52px;
  position: relative; }

#upload ul li input {
  display: none; }

#upload ul li p {
  width: 144px;
  overflow: hidden;
  white-space: nowrap;
  color: #EEE;
  font-size: 16px;
  font-weight: bold;
  position: absolute;
  top: 20px;
  left: 100px; }

#upload ul li i {
  font-weight: normal;
  font-style: normal;
  color: #7f7f7f;
  display: block; }

#upload ul li canvas {
  top: 15px;
  left: 32px;
  position: absolute; }

#upload ul li span {
  width: 15px;
  height: 12px;
  background: url("../img/icons.png") no-repeat;
  position: absolute;
  top: 34px;
  right: 33px;
  cursor: pointer; }

#upload ul li.working span {
  height: 16px;
  background-position: 0 -12px; }

#upload ul li.error p {
  color: red; }

/*----------------------------
    The Demo Footer
-----------------------------*/
footer {
  background-color: #080808;
  bottom: 0;
  box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.4);
  height: 40px;
  left: 0;
  position: fixed;
  width: 100%;
  z-index: 100000; }

footer h2 {
  font-size: 14px;
  font-weight: normal;
  position: absolute;
  top: 0;
  left: 50%;
  bottom: 0;
  margin-left: -420px;
  padding: 13px 20px 0;
  border: 1px solid #000;
  border-width: 0 1px;
  background-color: rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 5px #000 inset;
  line-height: 1;
  margin-top: 0; }

footer h2 a {
  color: #EEEEEE !important;
  text-decoration: none !important; }

footer h2 i {
  font-style: normal;
  color: #888; }

#tzine-download {
  opacity: 0.9;
  background-color: #257691;
  font-size: 11px;
  text-align: center;
  text-transform: uppercase;
  width: 150px;
  height: 28px;
  line-height: 28px;
  text-decoration: none !important;
  display: inline-block;
  border-radius: 2px;
  color: #fff !important;
  font-weight: bold;
  box-shadow: 0 -1px 2px #1e5e74 inset;
  border-top: 1px solid #26849c;
  text-shadow: 1px 1px 1px #1e6176;
  margin-top: 6px; }

#tzine-download:hover {
  opacity: 1; }

#tzine-actions {
  position: absolute;
  top: 0;
  width: 500px;
  right: 50%;
  margin-right: -420px;
  text-align: right; }

#tzine-actions iframe {
  display: inline-block;
  height: 21px;
  width: 95px;
  position: relative;
  float: left;
  margin-top: 11px; }
